/*    */

.modal-overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, #ffb8ca, #f0d1a5, #c2e297);
  z-index: 20;
  transition: 0.3s all;
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*      */
  width: 100%;
  max-width: 500px;

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 30px;
  border-radius: 24px;
  background-color: #fff5f6;
  transition: 0.3s all;
}

/*        */

.modal.active,
.modal-overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal-overlay.active {
  opacity: 0.5;
  pointer-events: none;
}

/*     */

.modal__cross {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #444;
  cursor: pointer;
}

/*   .    ,     */

a.js-open-modal {
  padding: 20px;
  display: inline-block;
  text-decoration: none;
  background-color: #414b52;
  margin: 10px;
  color: #fff;
  border-radius: 3px;
}
